import Vue from 'vue';
let qs = require('qs');

export default {     
    loginCheck(param){config
        return Vue.axios.post('/user/loginCheck',qs.stringify(param));
    },
    logout(param){
        return Vue.axios.post('/user/quit');
    },
    getLogin(param){
        return Vue.axios.post('/user/signin/passwd',param);
    },
    getImgCode(param){
        return Vue.axios.post('/user/imgcode/get',qs.stringify(param));
    },
    getPhoneCode(param){
        return Vue.axios.post('/user/vcode/get',param);
    },
    reg(param){
        return Vue.axios.post('/user/signup/passwd',param);
    },
    forgetPasswd(param){
        return Vue.axios.post('/user/passwd/forget',param);
    },
    resetPasswd(param){
        return Vue.axios.post('/user/passwd/reset',param);
    },
    verifyMobile(param){
        return Vue.axios.post('/user/mobile/verify',param);
    },
    getInfo(param){
        return Vue.axios.post('/user/info/get',param);
    },
    setInfo(param){
        return Vue.axios.post('/user/info/set',param);
    },
    modifyEmail(param){
        return Vue.axios.post('/user/email/modify',param);
    },
}


